import React from 'react'
import { Link } from 'react-router-dom'
import { Button } from 'semantic-ui-react'

const NoLogin = () => (
  <div>
    <p></p>
    <Button primary Inverted><Link to='/login'></Link></Button>
  </div>
)
export default NoLogin



